<template>
    <div>
        <el-main>
            <div class="select">
                <el-select v-model="department" clearable filterable placeholder="部门">
                    <el-option v-for="item in departmentList" :key="item.value" :label="item.label" :value="item.value"></el-option>
                </el-select>
                <el-select v-model="degree" clearable filterable placeholder="学历">
                    <el-option v-for="item in degreeList" :key="item.value" :label="item.label" :value="item.value"></el-option>
                </el-select>
                <el-input v-model="name" placeholder="姓名" clearable></el-input>
            </div>
            <el-table
                id="out-table"
                border
                ref="multipleTable"
                tooltip-effect="dark"
                style="width: 100%"
                @selection-change="handleSelectionChange"
                :header-cell-style="{background:'#f5f7fa'}"
                :default-sort="{prop: 'wid', order: 'descending'}"
                :data="tableData"
            >
                <el-table-column prop="wid" label="工号" min-width="100" sortable></el-table-column>
                <el-table-column prop="name" label="姓名" min-width="100"></el-table-column>
                <el-table-column
                    prop="sex"
                    label="性别"
                    min-width="100"
                    :filters="[{ text: '男', value: '男' }, { text: '女', value: '女' }]"
                    :filter-method="filterSex"
                    filter-placement="bottom-end"
                >
                    <template slot-scope="scope">
                        <el-tag
                            :type="scope.row.sex === '男' ? 'primary' : 'warning'"
                            disable-transitions
                        >{{scope.row.sex}}</el-tag>
                    </template>
                </el-table-column>
                <el-table-column prop="degree" label="学历" min-width="100"></el-table-column>
                <el-table-column prop="department" label="部门" min-width="100"></el-table-column>
                <el-table-column prop="baseWage" label="基本工资(月)" min-width="100"></el-table-column>
                <el-table-column  min-width="130" label="操作">
                    <template slot-scope="scope">
                        <el-button size="mini" type="primary" @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
                    </template>
                </el-table-column>
            </el-table>

            <el-pagination
                style="float: right;margin-top:8px;"
                background
                :current-page.sync="currentPage"
                @current-change="handleCurrentChange"
                layout="prev, pager, next"
                :page-size="7"
                :total="totalPage"
            ></el-pagination>
        </el-main>
        <edit :edititem="edititem" :editVisible="editVisible" @close-dialogStatus="Close_dialog"></edit>
        <file :fileitem="fileitem" :fileVisible="fileVisible" @close-dialogStatus="Close_dialog"></file>
    </div>
</template>
<style lang="stylus" scoped>
body {
    margin: 0;
}

.bottom {
    margin-top: 13px;
    line-height: 12px;
}

.button {
    padding: 0;
    float: right;
}

.image {
    width: 100%;
    height: 100%;
    display: block;
}

.clearfix:before, .clearfix:after {
    display: table;
    content: '';
}

.clearfix:after {
    clear: both;
}

.btn {
    margin-bottom: 10px;
}
.select {
    display: inline;
    margin-bottom: 10px;
}
.select, .el-input {
    width: 156px;
    margin-bottom 10px;
}
.el-date-editor--daterange.el-input, .el-date-editor--daterange.el-input__inner, .el-date-editor--timerange.el-input, .el-date-editor--timerange.el-input__inner{
    width 240px;
}
.bbb {
    cursor: text
    width: 41px
    height: 23px
    padding:0
    border 0px
}
</style>

<script>
import edit from "@/components/dialog/edit";
import file from "@/components/dialog/file";
import router from '../router';
import moment from "moment";
import FileSaver from "file-saver";
import XLSX from "xlsx";
export default {
    data() {
        return {
            search: "",
            edititem: {},
            editVisible: false,
            fileitem: {},
            fileVisible: false,
            currentPage: 1,
            totalPage: 1,

            departmentList: [
                {
                    label:"部门1",
                    value: "0"
                },
                {
                    label: "部门2",
                    value: "1"
                },
                {
                    label: "部门3",
                    value: "2"
                }
            ],
            department: "",

            degreeList: [
                { label:"博士研究生", value:"1" },
                { label:"本科毕业生", value:"3" },
                { label:"硕士研究生", value:"2" },
                { label:"专科毕业生", value:"4" },
                { label:"其他", value:"5" }
            ],
            degree: "",

            stime: "",
            etime: "",

            tableData: [],
            multipleSelection: []
        };
    },
    created: function() {
        this.td = this.tableData;
    },
    mounted: function() {},
    watch: {
        department(newValue, oldValue) {
            this.getList();
        },
        degree(newValue, oldValue) {
            this.getList();
        },
        search(newValue, oldValue) {
            this.getList();
        }
    },
    components: {
        edit,
        file
    },
    methods: {
        getList() {
            let here = this;
            axios({
                url: "/xclist",
                params: {
                    department: here.department,
                    degree: here.degree,
                    search: here.search,
                    currentPage: here.currentPage
                }
            })
                .then(function(response) {
                    let data = response.data;
                    here.tableData = data.data;
                    here.totalPage = data.totalPage;
                })
                .catch(function(error) {
                    console.log(error);
                });
        },
        Close_dialog(val) {
            this.editVisible = false;
            this.fileVisible = false;
        },
        handleSelectionChange(val) {
            this.multipleSelection = val;
        },
        filterSex(value, row) {
            return row.sex === value;
        },
        filterState(value, row) {
            return row.state === value;
        },
        add() {
            router.push({
                name:"tabs",
                params:{
                    data:'0'
                }
            });
        },
        handleEdit(index, row) {
            router.push({
                name:"tabs",
                params:{
                    data:row,
                    index:2
                }
            });
        },
        handleDelete(index, row) {
            if (row.state === "离职") {
                this.$message.error("该员工已离职!");
                return;
            }
            this.$confirm("是否解雇员工?", "提示", {
                confirmButtonText: "确定",
                cancelButtonText: "取消",
                type: "warning"
            }).then(() => {
                this.$message({
                    type: "success",
                    message: "删除成功!"
                });
                row.state = "离职";
                row.departureTime = moment(new Date()).format("YYYY-MM-DD");
            });
        },
        handleCurrentChange() {
            this.getList();
        },
        exportExcel() {
            /* 从表生成工作簿对象 */
            var wb = XLSX.utils.table_to_book(
                document.querySelector("#out-table")
            );
            /* 获取二进制字符串作为输出 */
            var wbout = XLSX.write(wb, {
                bookType: "xlsx",
                bookSST: true,
                type: "array"
            });
            try {
                FileSaver.saveAs(
                    //Blob 对象表示一个不可变、原始数据的类文件对象。
                    //Blob 表示的不一定是JavaScript原生格式的数据。
                    //File 接口基于Blob，继承了 blob 的功能并将其扩展使其支持用户系统上的文件。
                    //返回一个新创建的 Blob 对象，其内容由参数中给定的数组串联组成。
                    new Blob([wbout], { type: "application/octet-stream" }),
                    //设置导出文件名称
                    "user.xlsx"
                );
            } catch (e) {
                if (typeof console !== "undefined") console.log(e, wbout);
            }
            return wbout;
        }
    }
};
</script>



